import React from 'react'
import Fitness from '../img/fitness.png'
import { Card, Button, Stack } from 'react-bootstrap/';
// import { Container } from 'react-bootstrap';

function MernCard() {
  return (
    <div className='d-flex justify-content-center align-items-center mt-4'>

    <Card className='d-flex justify-content-center align-items-center text-center background-blur-container light-container-text' style={{ width: '60vw' }}>
      <Card.Img variant="top" src={Fitness} className='img-fluid rounded mt-3' style={{ 
        maxHeight: 210,
        width: '40vw',
      }}/>
      <Card.Body>
        <Card.Title></Card.Title>
        <Card.Text>
      A full-stack app that allows a user to pick from pre-seeded workouts, create new workouts, and delete user created workouts. Mobile first design, accordion-based UX, easy to navigate.
        </Card.Text>
        <Stack className='d-flex justify-content-center align-items-center'  direction="horizontal" gap={5}>
          <Button variant="secondary" href='https://github.com/MAMcC0/down-with-the-fitness'>View Repo</Button>
          <Button variant="secondary" href='https://down-with-the-fitness.herokuapp.com/'>View App</Button>
        </Stack>
      </Card.Body>
    </Card>
  </div>
  );
}

export default MernCard;